 .mini-nav {
   display: flex;
   flex-direction: row;
   align-items: center;
   background: #212121;
   width: 100%;
   height: 45px;
   color: white;
   padding-left: 160px;
   box-sizing: border-box;

   ul {


     li {
       display: flex;
       align-items: center;
       margin-left: 20px;

     }

     icons-style-mini {
        text: 8px;
        padding-top: 10px;

     }


   }


   .mini-text {
     display: flex;
     flex-direction: row;
   }
 }








