  .car-container-200 {
    margin: auto;
    width: 100%;
    background-color: black;
    margin-top: -5px;

  .car-financing-info-200 {
    height: 211.58px;
    background: white;
    width: 95%;
    margin:auto;
    display: flex;
    flex-direction: row;
    justify-content:space-around;
    align-items: center;




    .msrp-200 {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;


      .msrp-head-200 {
        font-weight: bold;
        font-size: 13px;
        margin-bottom: 15px;
      }

      .msrp-data-200 {
        font-weight: bold;
        font-size: 38px;


      }

      .msrp-foot-200 {
        font-weight: bold;
        font-size: 12px;
        padding-top: 11px;

      }

    }



    .lease-200 {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;


      .lease-head-200 {
        font-weight: bold;
        font-size: 13px;

      }

      .lease-info-200 {
        padding-top: 10px;


        h1 {
          font-size: 24px;
          font-weight: bold;
        }

        h2 {
          font-size: 16px;
        }


      }

      .lease-foot-200 {
        font-weight: bold;
        font-size: 12px;
        padding-top: 8px;


      }

    }


    .finance-200 {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;


      .finance-head-200 {
        font-weight: bold;
        font-size: 13px;

      }

      .finance-info-200 {
        font-weight: bold;
        font-size: 24px;
        padding-top: 10px;
      }


      .finance-months-200 {
        font-size: 13px;
        padding-top: 10px;


      }

      .finance-incentives-200 {
        font-size: 15px;
        padding-top:10px;

      }


      .get-up-to-link-200:hover {
        cursor: pointer;
      }

      .finance-link-200 {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        color: #4E68A9;
        font-size: 15px;
        font-weight: bold;
        padding-top: 8px;

        h2 {
          display: flex;
          align-items: center;
        }

      }

    }


    .get-up-to-200 {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;


      .get-up-to-head-200 {
        font-weight: bold;
        font-size: 13px;

      }

      .get-up-to-info-200 {
        font-weight: bold;
        font-size: 24px;
        padding-top: 10px;


      }

      .get-up-to-link-200:hover {
        cursor: pointer;
      }

      .get-up-to-link-200 {
        color: #4E68A9;
        font-weight: bold;
        font-size: 15px;
        padding-top: 8px;

          h2 {
            display: flex;
            align-items: center;
          }
      }

    }



    .finance-buttons-container-200 {

      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;



      .finance-build-button-200 {
        width: 70px;
        height: 35.3px;
        border: none;
        border-bottom-right-radius: 3px;
        border-top-right-radius: 3px;
        background-color: #3A57A0;
        color: #9CAACF;

        .icons-style-mini-finance {
          color: #7e7e7e;
          font-size: 14px;
          margin-left: 30px;
          margin-right: 8px;


          .finance-wrench {
            transform: scaleX(-1)
          }

        }



      }

      .finance-search-button-200 {
        font-weight: bold;
        font-size: 12px;
        padding-top: 8px;
        width: 100%;

      }

    }


  }


  .car-200:hover {
    cursor: pointer;
  }

  .car-packages {
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 95%;
    height: auto;
    background: #E7E7E7;
    color: #3A76C0;
    border-bottom: 6px solid black;

    .car-200 {
      height: 202.64px;
      width: 350.66px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-weight: bold;
      font-size: 11px;


      h2 {
        margin-top: 15px;
        font-weight: bolder;
        }

      }
  }



  .customize-container-200 {
    display: flex;
    flex-direction: row;
    margin: auto;
    width: 95%;
    background-color: black;
    margin-top: -5px;

    .customize-image-column-200 {
      height: 585.73px;
      background: white;
      width: 100%;
      margin: 0px;
      display: flex;
      flex-direction: column;
      justify-content:space-around;
      align-items: center;

      .customize-package-limited-200 {
        width: 809.91px;
        height: 200.91px;
        margin-top: -15px;

        img {
          width: 100%;
          height: auto;
        }
      }


      .limited-color-200:hover {
        cursor: pointer;
      }



      .customize-colors-limited-200 {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 511px;
        height: 55px;
        margin-top: 100px;

        .customize-colors-limited-200-border {
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          overflow: hidden;
          width: 40px;
          height: 40px;
          border-radius: 50%;
          border: .5px solid #232323;


          .limited-color-200 {
            overflow: hidden;
            width: 35px;
            height: 35px;
            border-radius: 50%;



            img {
              width: 100%;
              height: 100%;
            }
            }
            .bright-white {
            }

            .maximum-steel {
            }

            .billet-silver {
            }

            .granite-crystal {
            }

            .velvet-red {
            }

            .black {
            }

            .vivid-blue {
            }
          }
      }
    }
    .customize-info-column-200 {
      height: 585.73px;
      background: white;
      width: 95%;
      margin:auto;
      display: flex;
      flex-direction: column;
      justify-content:space-around;
      align-items: center;

      .customize-info-top-200 {
        padding-bottom: -40px;



        .car-package-year-200 {
          font-weight: bold;
        }

        .car-packaging-200 {
          font-size: 40px;
          text-transform: uppercase;
        }

        .car-packages-details-200 {
          line-height: 35px;
          font-size: 14px;
          letter-spacing: 1px;
          margin-bottom: -90px;

          .a-200 {
            list-style-type: disc;
            color: #636363;

            .b-200 {
              color: #636363;
            }
          }
        }

      }

      .customize-border-200 {
        display: flex;
        justify-content: flex-start;
        height: 2px;
        width: 92%;
        border-bottom-width: 1px;
        border-bottom-style: solid;
        border-bottom-color: #BEBEBE;
        margin-right: 50px;
        padding-top: -50px;

      }

      .customize-info-bottom-200 {
        display: flex;
        flex-direction: column;
        width: 585.73px;
        height: 72.98px;
        margin-top: -40px;

        .bottom-info-container-200 {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          margin-right: 50px;
          margin-top: -37px;

          .bottom-msrp-200 {
            justify-content: center;

          }

          .bottom-msrp-title-200 {
            font-size: 12px;
          }

          .bottom-msrp-data-200 {
            font-size: 35px;
            font-weight: bold;
          }
        }
      }
    }
  }
}
